
.cart-control {
  font-size: 0;
}
.cart-control .cart-decrease {
  display: inline-block;
  font-size: 24px;
}
.cart-control .cart-decrease .inner {
  display: inline-block;
  line-height: 24px;
  padding: 6px;
  color: #00a0dc;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cart-control .cart-decrease.move-enter-active,
.cart-control .cart-decrease .move-leave-active {
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cart-control .cart-decrease.move-enter,
.cart-control .cart-decrease .move-leave-to {
  opacity: 0;
  -webkit-transform: translate3D(24px, 0, 0);
          transform: translate3D(24px, 0, 0);
}
.cart-control .cart-decrease.move-enter .inner,
.cart-control .cart-decrease .move-leave-to .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cart-control .cart-increase {
  display: inline-block;
  font-size: 24px;
  line-height: 24px;
  padding: 6px;
  color: #00a0dc;
}
.cart-control .cart-count {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  font-size: 10px;
  color: #93999f;
}


