
.shop-cart {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 50;
  background: #000;
}
.shop-cart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #141d27;
  font-size: 0;
  color: rgba(255,255,255,0.4);
}
.shop-cart .content .content-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.shop-cart .content .content-left .logo-wrapper {
  display: inline-block;
  position: relative;
  top: -12px;
  margin: 0 12px;
  padding: 6px;
  width: 56px;
  height: 56px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  vertical-align: top;
  border-radius: 50%;
}
.shop-cart .content .content-left .logo-wrapper .logo {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #2b343c;
  text-align: center;
}
.shop-cart .content .content-left .logo-wrapper .logo.highlight {
  background: #00a0dc;
}
.shop-cart .content .content-left .logo-wrapper .logo .icon-shopping_cart {
  line-height: 44px;
  font-size: 24px;
  color: #80858a;
}
.shop-cart .content .content-left .logo-wrapper .logo .icon-shopping_cart.highlight {
  color: #fff;
}
.shop-cart .content .content-left .logo-wrapper .num {
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  border-radius: 16px;
  font-size: 9px;
  font-weight: 700;
  color: #fff;
  background-color: #f01414;
  -webkit-box-shadow: 0px 4px 8px 0 rgba(0,0,0,0.4);
          box-shadow: 0px 4px 8px 0 rgba(0,0,0,0.4);
}
.shop-cart .content .content-left .total-price {
  display: inline-block;
  vertical-align: top;
  margin-top: 12px;
  line-height: 24px;
  padding-right: 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-right: 1px solid rgba(255,255,255,0.1);
  font-size: 16px;
  font-weight: 700;
}
.shop-cart .content .content-left .total-price.highlight {
  color: #fff;
}
.shop-cart .content .content-left .delivery-price {
  display: inline-block;
  vertical-align: top;
  line-height: 24px;
  margin: 12px 0 0 12px;
  font-size: 10px;
}
.shop-cart .content .content-right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 105px;
          flex: 0 0 105px;
  width: 105px;
}
.shop-cart .content .content-right .pay {
  height: 55px;
  line-height: 55px;
  text-align: center;
  font-size: 12px;
  font-weight: 700;
}
.shop-cart .content .content-right .pay.below {
  background: #2b333b;
}
.shop-cart .content .content-right .pay.equal {
  background: #00b43c;
  color: #fff;
}


