
.product {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  position: absolute;
  width: 100%;
  top: 174px;
  bottom: 46px;
}
.product .menu-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 80px;
          flex: 0 0 80px;
  width: 80px;
  background: #f3f5f7;
}
.product .menu-wrapper .menu-item {
  display: table;
  height: 54px;
  width: 56px;
  line-height: 14px;
  padding: 0 12px;
}
.product .menu-wrapper .menu-item.current {
  position: relative;
  z-index: 10;
  margin-top: -1px;
  background: #fff;
  font-weight: 700;
}
.product .menu-wrapper .icon {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  height: 12px;
  background-size: 12px 12px;
}
.product .menu-wrapper .icon.decrease {
  background-image: url("decrease_3@2x.png") /* 默认dpr为2的设备*/;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.product .menu-wrapper .icon.decrease {
    background-image: url("decrease_3@3x.png");
}
}
.product .menu-wrapper .icon.discount {
  background-image: url("discount_3@2x.png") /* 默认dpr为2的设备*/;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.product .menu-wrapper .icon.discount {
    background-image: url("discount_3@3x.png");
}
}
.product .menu-wrapper .icon.guarantee {
  background-image: url("guarantee_3@2x.png") /* 默认dpr为2的设备*/;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.product .menu-wrapper .icon.guarantee {
    background-image: url("guarantee_3@3x.png");
}
}
.product .menu-wrapper .icon.invoice {
  background-image: url("invoice_3@2x.png") /* 默认dpr为2的设备*/;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.product .menu-wrapper .icon.invoice {
    background-image: url("invoice_3@3x.png");
}
}
.product .menu-wrapper .icon.special {
  background-image: url("special_3@2x.png") /* 默认dpr为2的设备*/;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.product .menu-wrapper .icon.special {
    background-image: url("special_3@3x.png");
}
}
.product .menu-wrapper .text {
  display: table-cell;
  vertical-align: middle;
  font-size: 12px;
  width: 56px;
}
.product .menu-wrapper .text:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.product .foods-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.product .foods-wrapper .title {
  padding-left: 14px;
  height: 26px;
  line-height: 26px;
  border-left: 2px solid #d9dde1;
  font-size: 12px;
  color: #93999f;
  background: #f3f5f7;
}
.product .foods-wrapper .food-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 18px;
  padding-bottom: 18px;
  position: relative;
}
.product .foods-wrapper .food-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.product .foods-wrapper .food-item:last-child {
  margin-bottom: 0;
}
.product .foods-wrapper .food-item .icon {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 57px;
          flex: 0 0 57px;
  margin-right: 10px;
}
.product .foods-wrapper .food-item .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.product .foods-wrapper .food-item .content .name {
  margin: 2px 0 8px 0;
  height: 14px;
  line-height: 14px;
  font-size: 14px;
  color: #07111b;
}
.product .foods-wrapper .food-item .content .desc,
.product .foods-wrapper .food-item .content .extra {
  line-height: 10px;
  font-size: 10px;
  color: #93999f;
}
.product .foods-wrapper .food-item .content .desc {
  margin-bottom: 8px;
  line-height: 12px;
}
.product .foods-wrapper .food-item .content .extra .count {
  margin-right: 8px;
}
.product .foods-wrapper .food-item .content .price {
  font-weight: 700;
  line-height: 24px;
}
.product .foods-wrapper .food-item .content .price .now {
  margin-right: 18px;
  font-size: 14px;
  color: #f01414;
}
.product .foods-wrapper .food-item .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.product .foods-wrapper .food-item .cartcontrol-wrapper {
  position: absolute;
  bottom: 10px;
  right: 0;
}


